<template>
  <v-container>
    <app-bar />
    <v-container style="display: flex">
      <v-btn to="/compare" fab class="ma-2" outlined color="blue" small>
        <v-icon>
          mdi-arrow-left
        </v-icon>
      </v-btn>
      <h1>{{ this.motor.brand + this.motor.model }}</h1>
    </v-container>
    <v-row>
      <v-col xs="12" lg="6">
        <v-card>
          <v-img :src=motor.image></v-img>
        </v-card>
      </v-col>
      <v-col lg="6">
        <h1>Motorbeschrijving</h1>
        {{ motor.description ? motor.description : "Geen beschrijving aanwezig" }}
      </v-col>
    </v-row>
    <v-row>
      <v-col lg="6">
        <v-container style="background-color: darkgray"><h2>Motorspecs</h2></v-container>
      </v-col>
    </v-row>
    <v-row>
      <v-col lg="6">
        <v-table>
          <tbody>
          <tr
              v-for="item in specs"
              :key="item.name"
          >
            <td>{{ item.name }}</td>
            <td>{{ item.spec }}</td>
          </tr>
          </tbody>
        </v-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import dataService from "@/services/DataService";
import AppBar from "@/components/app-bar-default.vue";

export default {
  name: "page-motorcyle",
  components: {AppBar},
  data() {
    return {
      motor: {},
      specs: [],
    }
  },
  mounted() {
    dataService.getMotorcycle(this.$route.params.id).then((response) => {
      this.motor = response.data;
      this.specs = [
        {
          name: 'Motor model',
          spec: this.motor.brand + this.motor.model,
        },
        {
          name: 'Motor type',
          spec: this.motor.motortype,
        },
        {
          name: 'Cilinderinhoud',
          spec: this.motor.cilinderSize + "cc",
        },
        {
          name: 'Vermogen in pk',
          spec: this.motor.powerInHp + "pk",
        },
        {
          name: 'Koppel in Nm',
          spec: this.motor.maxTorque + "Nm",
        },
        {
          name: 'Zithoogte in mm',
          spec: this.motor.seatHeight + "mm",
        },
        {
          name: 'Rijbewijstype',
          spec: this.motor.licenseType,
        },
      ]
    })
  }
}
</script>

