<template>
  <v-container>
    <app-bar @changeLicense="onLicenseChange" @changeSeatheight="onSeatHeightChange" @changeBrand="onBrandChange"
             @changeType="onTypeChange"/>
    <v-row>
      <v-col
          v-for="(motor, index) in motors"
          :key="index"
          col lg="3" md="6" sm="12"
      >
        <v-card class="mx-auto" min-width="100px" max-width="200px" height="200">
          <v-img height="120px" cover :src="motor.image"></v-img>
          <v-card-title>{{ motor.brand }} {{ motor.model }}</v-card-title>
          <v-card-subtitle>{{ motor.type }}</v-card-subtitle>
          <v-checkbox v-model="compareList" :disabled=checkDisabled(motor.id)  :value="motor.id" class="corner-checkbox"></v-checkbox>
        </v-card>
      </v-col>
    </v-row>
    <v-btn @click="compare" class="compare-button" v-if="compareList.length === 2"> <v-icon>mdi-compare</v-icon> Motoren vergelijken</v-btn>
    <v-pagination></v-pagination>
  </v-container>
</template>

<script>
import AppBar from "@/components/app-bar-comparison.vue";
import dataService from "@/services/DataService";

export default {
  name: 'ComparePage',
  components: {AppBar},
  data() {
    return {
      motors: [],
      initMotors: [],
      compareList: []
    }
  },
  mounted() {
    dataService.getAllMotorcycles().then((response) => {
      this.motors = response.data;
      this.initMotors = response.data;
    })
  },
  computed: {
    disableCheckboxes() {
      return this.compareList.length >= 2
    }
  },
  methods: {
    onLicenseChange(license) {
      if (license === "none" || license === "A(All)") {
        this.motors = this.initMotors;
      } else {
        dataService.getAllMotorcyclesByLicense(license)
            .then((res) => {
              this.motors = res.data;
            });
      }
    },
    onSeatHeightChange(value) {
      dataService.getAllMotorcyclesBySeatHeight(value)
          .then((res) => {
            this.motors = res.data;
          });
    },
    onBrandChange(value) {
      if (value === "none") {
        this.motors = this.initMotors;
      } else {
        dataService.getAllMotorcyclesByBrand(value)
            .then((res) => {
              this.motors = res.data;
            });
      }
    },
    onTypeChange(value) {
      if (value === "none") {
        this.motors = this.initMotors;
      } else {
        dataService.getAllMotorcyclesByType(value)
            .then((res) => {
              this.motors = res.data;
            });
      }
    },
    compare() {
      if (this.compareList.length === 2) {
        this.$router.push({path: '/motorcycle/' + this.compareList[0] + '/' + this.compareList[1]})
      }
    },
    valueToCurrency(value) {
      if (typeof value !== "number") {
        return value;
      }
      var formatter = new Intl.NumberFormat('nl-NL', {
        style: 'currency',
        currency: 'EUR'
      });
      return formatter.format(value);
    },
    checkDisabled(value) {
      return this.disableCheckboxes && !this.compareList.includes(value)
    }
  }
}
</script>
<style>
.corner-checkbox {
  position: absolute;
  bottom: -30px;
  right: 10px;
}
.compare-button {
  position: fixed;
  top: 100px;
  right: 10px;
  background-color: orange;
}
</style>
